import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Table from "./VideoPartTable";
import Cookies from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from './SideNav';
import Loading from './Loading';


export default function Videos(props) {


  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [allCategories, setAllCategories] = useState([]);
  const [showCategoryEditModal, setShowCategoryEditModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [addCategoryObj, setAddCategoryObj] = useState({});
  const [showLoading, setShowLoading] = useState(false);
  const [showAddVideoModal, setShowAddVideoModal] = useState(false);
  const [categoryId, setCategoryId] = useState("");
  useEffect(() => {
    if (!getCategoryId2()) { window.location.href = '/videos' }
    getCategoryId();
    getAllVideoParts();
    $('#myModal').on('shown.bs.modal', function () {
      $('#myInput').trigger('focus')
    })
    return () => {

    }
  }, [])



  const getAllVideoParts = async () => {
    setShowLoading(true);
    const getAllCategoryRequest = await Axios.post(config.serverApi + 'getallvideoparts',
      {
        token: Cookies.get('token'),
        videoId: getCategoryId2(),
      })
    if (getAllCategoryRequest.status == "200") {
      getAllCategoryRequest.data.data.map((element) => {

        element.operations = <div class="col4"><Button onClick={() => openEditVideoPartModal(element)} type="button" class="btn btn-primary">Edit</Button>  <Button type="button" onClick={() => removeCategory(element._id)} className="btn btn-danger">Remove</Button></div>

      })

      // getAllCategoryRequest.data.data.sort((a, b) => (a.categoryNumber > b.categoryNumber) ? 1 : ((b.categoryNumber > a.categoryNumber) ? -1 : 0));
      setCategories(getAllCategoryRequest.data.data)
    }
    setTimeout(() => setShowLoading(false), 500)
  }










  const addVideo = async () => {

    const adddCategoryRequest = await Axios.post(config.serverApi + 'addvideopart',
      {
        token: Cookies.get('token'),
        videoId: getCategoryId2(),
        minute: addCategoryObj.minute,
        text: addCategoryObj.text,
      })
    closeAddVideoModal();
    // setCategories((prev) => prev.concat({ categoryNumber: categoryNumber, categoryName: value }));
    getAllVideoParts();

  };

  const editCategoryModal = () =>

    <Modal show={showCategoryEditModal}>
      <Modal.Header>Edit Video</Modal.Header>
      <Modal.Body>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Minute</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Number"
            value={editCurrentCategory.minute}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, minute: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Text &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            value={editCurrentCategory.text}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, text: element.target.value }))}
          />
        </InputGroup>





      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => saveEditCategory()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeEditCategoryModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const addNewCategoryModal = () =>

    <Modal show={showAddVideoModal}>
      <Modal.Header>Add Video Part</Modal.Header>
      <Modal.Body>


        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Minute &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Minute"
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, minute: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Text &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            // value={editCurrentCategory.videoSource}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, text: element.target.value }))}
          />
        </InputGroup>


      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => addVideo()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeAddVideoModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const openEditVideoPartModal = (category) => {
    setShowCategoryEditModal(true);
    setEditCurrentCategory(category);
  }

  const closeEditCategoryModal = () => {
    setShowCategoryEditModal(false);
  }
  const closeAddVideoModal = () => {
    setShowAddVideoModal(false);
  }
  const saveEditCategory = async () => {


    const editCategoryRequest = await Axios.post(config.serverApi + 'updatevideopart',
      {
        videoPartId: editCurrentCategory._id,
        minute: editCurrentCategory.minute,
        text: editCurrentCategory.text,
      });
    closeEditCategoryModal();
    getAllVideoParts();
  }

  const removeCategory = async (_id) => {
    const removeCategoryRequest = await Axios.post(config.serverApi + 'deletevideopart',
      {
        videoPartId: _id
      });
    closeEditCategoryModal();
    getAllVideoParts();
  }

  if (Cookies.get('token'))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="videoparts" />
        {editCategoryModal()}
        {addNewCategoryModal()}
        <Container className="wrapper">

          <Row>
            <Col>
              <h1 className="text">Video Parts</h1>
            </Col>
          </Row>
          <Row>

            <Col><Button className="btn btn-success" onClick={() => setShowAddVideoModal(true)}>Add Video Parts</Button><br /><br /></Col>



            <style jsx>{`
            .controlheader
            {
              display:flex
            }
            .wrapper {
              padding: 4rem;
              border: 0;
              width: 80%;
              position: absolute;
              right: 0;
            }
            .text {
              margin-left: 18rem;
              margin-bottom: 4rem;
            }
            .catData {
              transform: translateY(-2.5rem);
            }
            .catButton
            {
              margin-bottom:20px
            }
            table{
              text-align:center;
            }
          `}</style>
          </Row>
          <Row>
            <Col>
              <Table categories={categories} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  else
    window.location.href = '/'



  function getCategoryId() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    setCategoryId(urlParams.get('video_id'));
    setAddCategoryObj(prev => ({ ...prev, videoId: urlParams.get('video_id') }))
    return urlParams.get('video_id');
  }
  function getCategoryId2() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    return urlParams.get('video_id');
  }
}



