import React from "react";
import { useTable } from "react-table";

export default function Table(props) {
  const data = props.categories;

  const columns = React.useMemo(
    () => [
      {
        Header: "Minute",
        accessor: "minute",
      },
      {
        Header: "Text",
        accessor: "text",
      },
      {
        Header: "Operations",
        accessor: "operations",
      },
    ],
    []
  );

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({ columns, data });

  return (
    <div>
      <table {...getTableProps()} class="table table-striped table-bordered">
        <thead class="thead-dark">
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th
                  {...column.getHeaderProps()}

                >
                  {column.render("Header")}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {data.length < 1 && (<tr><td colSpan="4"><center>Category is empty</center></td></tr>)}

          {rows.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return (
                    <td
                      {...cell.getCellProps()}

                    >
                      {cell.render("Cell")}
                    </td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
