import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Cookies from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from './SideNav';
import Loading from './Loading';


export default function Categories() {
  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [showEditModal, setShowEditModal] = useState(false);
  const [showAddModal, setShowAddModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [showLoading, setShowLoading] = useState(false);
  const [editPrice, setEditPrice] = useState({});
  const [addNewPrice, setAddNewPrice] = useState({});
  const [prices, setPrices] = useState([]);



  useEffect(() => {
    getAllPrices();
    $('#myModal').on('shown.bs.modal', function () {
      $('#myInput').trigger('focus')
    })
    return () => {

    }
  }, [])
  const getAllPrices = async () => {
    setShowLoading(true);
    const getSettings = await Axios.get(config.serverApi + 'getprices', { params: { lang: "all" } });
    if (getSettings) {
      const settingsData = getSettings.data.prices;
      setPrices(settingsData);
      setShowLoading(false);
    }
  }

  // const editPriceFunc


  const closeAddModal = () => {
    setShowAddModal(false);
  }
  const closeEditModal = () => {
    setShowEditModal(false);
  }

  const addNewPriceFunc = async (lang) => {
    setShowLoading(true);
    const editCategoryRequest = await Axios.post(config.serverApi + 'addprice',
      {
        lang: lang,
        ...addNewPrice
      });
    closeAddModal();
    getAllPrices();
    setShowLoading(false);
  }

  const editPriceFunc = async () => {
    setShowLoading(true);
    const editCategoryRequest = await Axios.post(config.serverApi + 'changeprices',
      {
        priceId: editPrice.id,
        ...editPrice
      });
    closeEditModal();
    getAllPrices();
    setShowLoading(false);
  }

  const deletePrice = async (priceId) => {
    setShowLoading(true);
    const removeCategoryRequest = await Axios.post(config.serverApi + 'deleteprice',
      {
        priceId: priceId
      });
    closeEditModal();
    getAllPrices();
    setShowLoading(false);
  }





  const addNewPriceModal = (lang) =>

    <Modal show={showAddModal}>
      <Modal.Header>Add New Price</Modal.Header>
      <Modal.Body>


        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Month &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Month"
            onChange={(element) => setAddNewPrice(prev => ({ ...prev, month: Number(element.target.value) }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Price &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            // value={editCurrentCategory.videoSource}
            onChange={(element) => setAddNewPrice(prev => ({ ...prev, price: Number(element.target.value) }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">currency &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <select class="form-control" id="exampleFormControlSelect1"
            onChange={(element) => setAddNewPrice(prev => ({ ...prev, currency: element.target.value }))}>
            <option value="USD" selected>USD</option>
            <option value="TL" >TL</option>
            <option value="EUR" >EUR</option>
            <option value="GBP" >GBP</option>
            <option value="RUB" >RUB</option>
            <option value="CHF" >CHF</option>
            <option value="NOK" >NOK</option>
            <option value="IRR" ÃŸ>IRR</option>
          </select>
        </InputGroup>


      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => addNewPriceFunc(lang)}>Add</Button>
        <Button className="btn btn-danger" onClick={() => closeAddModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const editPriceModal = () =>

    <Modal show={showEditModal}>
      <Modal.Header>Edit  Price</Modal.Header>
      <Modal.Body>


        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Month &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Month"
            value={editPrice.month}
            onChange={(element) => setEditPrice(prev => ({ ...prev, month: Number(element.target.value) }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Price &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            value={editPrice.price}
            onChange={(element) => setEditPrice(prev => ({ ...prev, price: Number(element.target.value) }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">currency &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <select class="form-control" id="exampleFormControlSelect1"
            onChange={(element) => setEditPrice(prev => ({ ...prev, currency: element.target.value }))}>
            <option value="USD" selected={editPrice.currency == 'USD'}>USD</option>
            <option value="TL" selected={editPrice.currency == 'TL'}>TL</option>
            <option value="EUR" selected={editPrice.currency == 'EUR'}>EUR</option>
            <option value="GBP" selected={editPrice.currency == 'GBP'}>GBP</option>
            <option value="RUB" selected={editPrice.currency == 'RUB'}>RUB</option>
            <option value="CHF" selected={editPrice.currency == 'CHF'}>CHF</option>
            <option value="NOK" selected={editPrice.currency == 'NOK'}>NOK</option>
            <option value="IRR" selected={editPrice.currency == 'IRR'}>IRR</option>
          </select>




        </InputGroup>


      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => editPriceFunc()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeEditModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const priceComponent = (lang) => {
    let currentPriceList = prices.filter(e => e.lang == lang);
    if (currentPriceList.length < 1) return (
      <div class="row" style={{ borderWidth: 1, borderColor: '#CCC', borderStyle: 'solid', padding: 21, marginRight: 5, marginLeft: 5 }}>
        <span class="input-group-text col" id="inputGroup-sizing-default">No Price Found for this language</span>
        <div class="input-group mb-3 row" style={{ marginTop: 10 }} >
          &nbsp; &nbsp;  <Button onClick={() => { setAddNewPrice({ lang: lang, currency: "USD" }); setShowAddModal(true) }} class="input-group-text col" id="inputGroup-sizing-default">Add New Price</Button>
        </div >
      </div>
    )
    else return (
      <>
        {currentPriceList.map((price) =>

          <div class="row" style={{ borderWidth: 1, borderColor: '#CCC', borderStyle: 'solid', padding: 21, marginRight: 5, marginLeft: 5 }}>
            <span class="input-group-text col" id="inputGroup-sizing-default">{price.month} Months</span> &nbsp;&nbsp;
          <span class="input-group-text col" id="inputGroup-sizing-default">{price.price} {price.currency}</span>
         &nbsp;&nbsp;&nbsp;
         <Button onClick={() => { setEditPrice(price); setShowEditModal(true) }} class="input-group-text" id="inputGroup-sizing-default">Edit</Button>
         &nbsp;&nbsp;&nbsp;
          <Button onClick={() => deletePrice(price._id)} className="btn btn-danger" >Delete</Button>
          </div>

        )}
        <div class="input-group mb-3 row" style={{ marginTop: 10 }} >
          &nbsp; &nbsp; &nbsp;<Button onClick={() => { setAddNewPrice({ lang: lang, currency: "USD" }); setShowAddModal(true) }} id="inputGroup-sizing-default">Add New Price</Button>
        </div >
      </>)
  }



  if (Cookies.get('token'))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="prices" />
        {editPriceModal()}
        {addNewPriceModal()}
        <Container className="wrapper">


          <Row>
            <Col>
              <h1 className="text">Prices</h1>
            </Col>
          </Row>
          <Row>


            <div class="container">
              <div class="row">
                <div class="col-sm">
                  <h3><center>Turkish Prices</center></h3>
                  {priceComponent("tr")}
                </div>
                <div class="col-sm">
                  <h3><center>Russian Prices</center></h3>
                  {priceComponent("ru")}
                </div>
              </div>

              <div class="row">
                <div class="col-sm">
                  <h3><center>Persian Prices</center></h3>
                  {priceComponent("fa")}
                </div>
                <div class="col-sm">
                  <h3><center>English Prices</center></h3>
                  {priceComponent("en")}
                </div>
              </div>


              <div class="row">


                <div class="col-sm"></div>



              </div>
            </div>


            <style jsx>{`
            .wrapper {
              padding: 4rem;
              border: 0;
              width: 80%;
              position: absolute;
              right: 0;
            }
            .text {
              margin-left: 18rem;
              margin-bottom: 4rem;
            }
            .catData {
              transform: translateY(-2.5rem);
            }
            .catButton
            {
              margin-bottom:20px
            }
            table{
              text-align:center;
            }
          `}</style>
          </Row>

        </Container>

      </div >
    );
  else
    window.location.href = '/'


}



