import React, { useState, useEffect } from "react";
import { Form, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import Cookies from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import { useHistory } from 'react-router-dom';
import { propTypes } from "react-bootstrap/esm/Image";

export default function SideNav(props) {
    //   const [email, setEmail] = useState("");
    //   const [password, setPassword] = useState("");


    return (
        <div>
            <div class="sidebar">
                <h1>Dr.Patris Admin Panel</h1>

                <ul class="sidebarul">
                    <Link to="/categories"><li class={props.page == 'categories' ? 'selectednav' : ''}>Category</li></Link>
                    <Link to="/videos"><li class={props.page == 'videos' ? 'selectednav' : ''}>Videos</li></Link>
                    <Link to="/videoparts"><li class={props.page == 'videoparts' ? 'selectednav' : ''}>Video Parts</li></Link>
                    <Link to="/admin"><li class={props.page == 'admin' ? 'selectednav' : ''}>Admin Users</li></Link>
                    <Link to="/users"><li class={props.page == 'users' ? 'selectednav' : ''}>Users</li></Link>
                    <Link to="/prices"><li class={props.page == 'prices' ? 'selectednav' : ''}>Prices</li></Link>
                    <Link to="/settings"><li class={props.page == 'settings' ? 'selectednav' : ''}>Settings</li></Link>
                    <li onClick={() => { Cookies.remove('token'); window.location.href = '/' }}>LogOut</li>
                </ul>
            </div>



            <style jsx>{`
        .sidebar h1{
            color:#FFF;
            text-align:center;
            margin: 30px;
        }
        .sidebar {
            width: 20%;
            background: #333b3f;
            position: absolute;
            left: 0;
            height: 100%;
            top: 0;
        }
        .sidebarul{
            padding:0;
            border-top:1px solid #24292b;
            cursor:pointer
        }
        .sidebarul li{
            list-style: none;
            padding: 17px;
            text-align: center;
            font-size:20px;
            border-bottom:1px solid #24292b;
            text-decoration: none;
        }

        .sidebarul li:hover {
            background: #24292b;
            color:#FFF;
        }
        .selectednav{
            background: #24292b;
            color:#FFF;
        }
        
        ul li
        {
            color:#CCC
        }
      `}</style>
        </div>
    );
}
