import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Table from "./VideoTable";
import Cookies from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import $, { param } from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from './SideNav';
import Loading from './Loading';


export default function Videos(props) {


  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [allCategories, setAllCategories] = useState([]);
  const [showCategoryEditModal, setShowCategoryEditModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [addCategoryObj, setAddCategoryObj] = useState({});
  const [showLoading, setShowLoading] = useState(false);
  const [showAddVideoModal, setShowAddVideoModal] = useState(false);
  const [categoryId, setCategoryId] = useState("");
  useEffect(() => {
    getCategoryId();
    getAllVideos();
    getAllCategories();
    $('#myModal').on('shown.bs.modal', function () {
      $('#myInput').trigger('focus')
    })
    return () => {

    }
  }, [])



  const getAllVideos = async () => {
    setShowLoading(true);
    const getAllCategoryRequest = await Axios.post(config.serverApi + 'getallvideos',
      {
        token: Cookies.get('token'),
        categoryId: getCategoryId2(),
      })

    if (getAllCategoryRequest.status == "200") {
      getAllCategoryRequest.data.data.map((element) => {

        element.operations = <div class="col4"><Button onClick={() => window.location.href = 'videoparts?video_id=' + element._id} type="button" className="btn btn-success">Video Parts</Button>   <Button onClick={() => openEditCategoryModal(element)} type="button" class="btn btn-primary">Edit</Button>  <Button type="button" onClick={() => removeCategory(element._id)} className="btn btn-danger">Remove</Button></div>

      })

      // getAllCategoryRequest.data.data.sort((a, b) => (a.categoryNumber > b.categoryNumber) ? 1 : ((b.categoryNumber > a.categoryNumber) ? -1 : 0));
      setCategories(getAllCategoryRequest.data.data)
    }
    setTimeout(() => setShowLoading(false), 500)
  }

  const getAllCategories = async () => {
    var language = "";
    if (getCategoryId2()) {
      const getCurrentCategory = await Axios.get(config.serverApi + 'getcategory', { params: { categoryId: getCategoryId2() } })
      language = getCurrentCategory.data.category.lang;

    }
    const getAllCategoryRequest = await Axios.get(config.serverApi + 'getallcategories',
      {
        params: {
          token: Cookies.get('token'),
          lang: language
        }
      })
    setAllCategories(getAllCategoryRequest.data.data)

    if (getCategoryId2() == null) window.location.href = 'videos?category_id=' + getAllCategoryRequest.data.data[0]._id
  }






  const addVideo = async () => {
    let videoNumber = 0;
    if (categories.length > 0) {
      categories.map(element => {
        if (element.videoNumber > videoNumber) {
          videoNumber = element.videoNumber; // Sonuncuyu bulmak iÃ§in
        }
      })
    }
    videoNumber++;

    const adddCategoryRequest = await Axios.post(config.serverApi + 'addvideo',
      {
        token: Cookies.get('token'),
        categoryId: addCategoryObj.categoryId,
        videoNumber: videoNumber,
        videoName: addCategoryObj.videoName,
        videoSource: addCategoryObj.videoSource,
        freeTrial: addCategoryObj.freeTrial
      })
    closeAddVideoModal();
    // setCategories((prev) => prev.concat({ categoryNumber: categoryNumber, categoryName: value }));
    getAllVideos();

  };

  const editCategoryModal = () =>

    <Modal show={showCategoryEditModal}>
      <Modal.Header>Edit Video</Modal.Header>
      <Modal.Body>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Number</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Number"
            value={editCurrentCategory.videoNumber}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, videoNumber: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Name &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            value={editCurrentCategory.videoName}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, videoName: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Source &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            value={editCurrentCategory.videoSource}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, videoSource: element.target.value }))}
          />
        </InputGroup>



        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Category</InputGroup.Text>
          </InputGroup.Prepend>

          <select class="form-control" id="exampleFormControlSelect1"
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, categoryId: element.target.value }))}>
            {allCategories.map(element =>
              <option value={element._id} selected={editCurrentCategory.categoryId == element._id}>{element.categoryName}</option>
            )

            }
          </select>
        </InputGroup>


        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Free Trial &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;</InputGroup.Text>
          </InputGroup.Prepend>

          <select class="form-control" id="exampleFormControlSelect1"
            defaultValue={editCurrentCategory.freeTrial}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, freeTrial: element.target.value }))}>

            <option value={true} selected={editCurrentCategory.freeTrial}>Yes</option>
            <option value={false} selected={editCurrentCategory.freeTrial}>No</option>
          </select>
        </InputGroup>




      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => saveEditCategory()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeEditCategoryModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const addNewCategoryModal = () =>

    <Modal show={showAddVideoModal}>
      <Modal.Header>Add Video</Modal.Header>
      <Modal.Body>

        {/* <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Number</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Number"
            value={editCurrentCategory.videoNumber}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, videoNumber: element.target.value }))}
          />
        </InputGroup> */}

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Name &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            // value={editCurrentCategory.videoName}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, videoName: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Source &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            // value={editCurrentCategory.videoSource}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, videoSource: element.target.value }))}
          />
        </InputGroup>



        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Category</InputGroup.Text>
          </InputGroup.Prepend>

          <select class="form-control" id="exampleFormControlSelect1"
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, categoryId: element.target.value }))}>
            {allCategories.map(element =>
              <option value={element._id} selected={getCategoryId2() == element._id}>{element.categoryName}</option>
            )

            }
          </select>
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Free Trial &nbsp; &nbsp; &nbsp; &nbsp;&nbsp; &nbsp;</InputGroup.Text>
          </InputGroup.Prepend>

          <select class="form-control" id="exampleFormControlSelect1"
            defaultValue={true}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, freeTrial: element.target.value }))}>

            <option value={true} selected={true}>Yes</option>
            <option value={false} selected={false}>No</option>
          </select>
        </InputGroup>



      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => addVideo()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeAddVideoModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const openEditCategoryModal = (category) => {
    setShowCategoryEditModal(true);
    setEditCurrentCategory(category);
  }

  const closeEditCategoryModal = () => {
    setShowCategoryEditModal(false);
  }
  const closeAddVideoModal = () => {
    setShowAddVideoModal(false);
  }
  const saveEditCategory = async () => {
    const editCategoryRequest = await Axios.post(config.serverApi + 'updatevideo',
      {
        videoId: editCurrentCategory._id,
        categoryId: editCurrentCategory.categoryId,
        videoName: editCurrentCategory.videoName,
        videoNumber: editCurrentCategory.videoNumber,
        videoSource: editCurrentCategory.videoSource,
        freeTrial: editCurrentCategory.freeTrial
      });
    closeEditCategoryModal();
    getAllVideos();
  }

  const removeCategory = async (_id) => {
    const removeCategoryRequest = await Axios.post(config.serverApi + 'deletevideo',
      {
        videoId: _id
      });
    closeEditCategoryModal();
    getAllVideos();
  }

  if (Cookies.get('token'))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="videos" />
        {editCategoryModal()}
        {addNewCategoryModal()}
        <Container className="wrapper">

          <Row>
            <Col>
              <h1 className="text">Videos</h1>
            </Col>
          </Row>
          <Row>
            <Col >
              <div class="controlheader">

                <InputGroup className="mb-3">
                  <InputGroup.Prepend>
                    <InputGroup.Text id="basic-addon1">Video Category</InputGroup.Text>
                  </InputGroup.Prepend>

                  <select class="form-control" id="exampleFormControlSelect1"
                    onChange={(element) => window.location.href = 'videos?category_id=' + element.target.value}>
                    {allCategories.map(element =>
                      <option value={element._id} selected={getCategoryId2() == element._id}>{element.categoryName}</option>
                    )

                    }
                  </select>
                </InputGroup>


              </div>
            </Col>
            <Col><Button className="btn btn-success" onClick={() => setShowAddVideoModal(true)}>Add Video</Button></Col>



            <style jsx>{`
            .controlheader
            {
              display:flex
            }
            .wrapper {
              padding: 4rem;
              border: 0;
              width: 80%;
              position: absolute;
              right: 0;
            }
            .text {
              margin-left: 18rem;
              margin-bottom: 4rem;
            }
            .catData {
              transform: translateY(-2.5rem);
            }
            .catButton
            {
              margin-bottom:20px
            }
            table{
              text-align:center;
            }
          `}</style>
          </Row>
          <Row>
            <Col>
              <Table categories={categories} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  else
    window.location.href = '/'



  function getCategoryId() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    setCategoryId(urlParams.get('category_id'));
    setAddCategoryObj(prev => ({ ...prev, categoryId: urlParams.get('category_id') }))
    return urlParams.get('category_id');
  }
  function getCategoryId2() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    return urlParams.get('category_id');
  }
}



