import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Table from "./CategoryTable";
import Cookies from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from './SideNav';
import Loading from './Loading';


export default function Categories() {
  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [showCategoryEditModal, setShowCategoryEditModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [categoryLanguage, setCategoryLanguage] = useState("en");
  const [showLoading, setShowLoading] = useState(false);

  useEffect(() => {
    getAllCategories();
    $('#myModal').on('shown.bs.modal', function () {
      $('#myInput').trigger('focus')
    })
    return () => {

    }
  }, [])

  useEffect(() => {
    getAllCategories()
  }, [categoryLanguage])

  const getAllCategories = async () => {
    setShowLoading(true);
    const getAllCategoryRequest = await Axios.get(config.serverApi + 'getallcategories',
      {
        params: {
          token: Cookies.get('token'),
          lang: categoryLanguage
        }
      })


    if (getAllCategoryRequest.status == "200") {
      getAllCategoryRequest.data.data.map((element) => {

        element.operations = <div class="col4"><Button onClick={() => window.location.href = 'videos?category_id=' + element._id} type="button" className="btn btn-success">Videos</Button>   <Button onClick={() => openEditCategoryModal(element)} type="button" class="btn btn-primary">Edit</Button>  <Button type="button" onClick={() => removeCategory(element._id)} className="btn btn-danger">Remove</Button></div>

      })

      // getAllCategoryRequest.data.data.sort((a, b) => (a.categoryNumber > b.categoryNumber) ? 1 : ((b.categoryNumber > a.categoryNumber) ? -1 : 0));
      setCategories(getAllCategoryRequest.data.data)
    }
    setTimeout(() => setShowLoading(false), 500)
  }

  const categoryHandler = (value) => {
    setValue(value.target.value);
  };


  const addCategories = async () => {
    let categoryNumber = 0;
    if (categories.length > 0) {
      categories.map(element => {
        if (element.categoryNumber > categoryNumber) {
          categoryNumber = element.categoryNumber;
        }
      })
    }

    categoryNumber++;

    const adddCategoryRequest = await Axios.post(config.serverApi + 'addcategory',
      {
        token: Cookies.get('token'),
        categoryName: value,
        categoryNumber: categoryNumber,
        lang: categoryLanguage
      })
    // setCategories((prev) => prev.concat({ categoryNumber: categoryNumber, categoryName: value }));
    getAllCategories();

  };

  const editCategoryModal = () =>

    <Modal show={showCategoryEditModal}>
      <Modal.Header>Edit category</Modal.Header>
      <Modal.Body>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Category Number</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Number"
            value={editCurrentCategory.categoryNumber}
            onChange={(newCategoryNumber) => setEditCurrentCategory(prev => ({ ...prev, categoryNumber: newCategoryNumber.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Category Name &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            value={editCurrentCategory.categoryName}
            onChange={(newCategoryName) => setEditCurrentCategory(prev => ({ ...prev, categoryName: newCategoryName.target.value }))}
          />
        </InputGroup>




      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => saveEditCategory()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeEditCategoryModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>

  const openEditCategoryModal = (category) => {
    setShowCategoryEditModal(true);
    setEditCurrentCategory(category);
  }

  const closeEditCategoryModal = () => {
    setShowCategoryEditModal(false);
  }
  const saveEditCategory = async () => {
    const editCategoryRequest = await Axios.post(config.serverApi + 'updatecategory',
      {
        categoryId: editCurrentCategory._id,
        categoryName: editCurrentCategory.categoryName,
        categoryNumber: editCurrentCategory.categoryNumber
      });
    closeEditCategoryModal();
    getAllCategories();
  }

  const removeCategory = async (categoryId) => {
    const removeCategoryRequest = await Axios.post(config.serverApi + 'deletecategory',
      {
        categoryId: categoryId
      });
    closeEditCategoryModal();
    getAllCategories();
  }

  if (Cookies.get('token'))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="categories" />
        {editCategoryModal()}

        <Container className="wrapper">





          <Row>
            <Col>
              <h1 className="text">Categories</h1>

            </Col>
            <Col>
              <select class="form-control" id="exampleFormControlSelect1"
                onChange={(element) => { setCategoryLanguage(element.target.value); }}>
                <option value="en">English</option>
                <option value="tr">Turkish</option>
                {/* <option value="ar">Arabic</option> */}
                <option value="ru">Russian</option>
                <option value="fa">Persian</option>
              </select>
            </Col>
          </Row>



          <Row>
            <Col >
              <InputGroup className="catButton">
                <FormControl
                  placeholder="Category Name"
                  aria-describedby="basic-addon2"
                  // value={value}
                  onChange={categoryHandler}
                />
                <br></br>

                <InputGroup.Append>
                  <Button variant="success" onClick={addCategories}>
                    Add
                </Button>
                </InputGroup.Append>
              </InputGroup>
            </Col>

            <Col className="catData">

            </Col>

            <style jsx>{`
            .wrapper {
              padding: 4rem;
              border: 0;
              width: 80%;
              position: absolute;
              right: 0;
            }
            .text {
              margin-left: 18rem;
              margin-bottom: 4rem;
            }
            .catData {
              transform: translateY(-2.5rem);
            }
            .catButton
            {
              margin-bottom:20px
            }
            table{
              text-align:center;
            }
          `}</style>
          </Row>
          <Row>
            <Col>
              <Table categories={categories} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  else
    window.location.href = '/'


}



