import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Cookies from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from './SideNav';
import Loading from './Loading';


export default function Categories() {
  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [showCategoryEditModal, setShowCategoryEditModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [showLoading, setShowLoading] = useState(false);

  const [mainColor, setMainColor] = useState("");
  const [secondColor, setSecondColor] = useState("");
  const [shareButton, setShareButton] = useState("");
  const [profileColor, setProfileColor] = useState("");
  const [faqColor, setFaqColor] = useState("");
  const [savedColor, setSavedColor] = useState("");
  const [contact, setContact] = useState("");
  const [navigationColor, setNavigationColor] = useState("");


  useEffect(() => {
    getAllCategories();
    getSettings();
    $('#myModal').on('shown.bs.modal', function () {
      $('#myInput').trigger('focus')
    })
    return () => {

    }
  }, [])

  const getSettings = async () => {
    setShowLoading(true);
    const getSettings = await Axios.get(config.serverApi + 'getsettings');
    if (getSettings) {
      const settingsData = getSettings.data.settings;
      console.log(settingsData)
      setMainColor(settingsData.mainColor);
      setSecondColor(settingsData.secondColor);
      setShareButton(settingsData.shareButton);
      setProfileColor(settingsData.profileColor);
      setFaqColor(settingsData.faqColor);
      setSavedColor(settingsData.savedColor);
      setContact(settingsData.contact);
      setNavigationColor(settingsData.navigationColor);

    }


  }


  const changeSettings = async () => {
    setShowLoading(true);
    const changeSettingRequest = await Axios.post(config.serverApi + 'changesettings', {
      mainColor,
      secondColor,
      shareButton,
      profileColor,
      faqColor,
      savedColor,
      contact,
      navigationColor
    });
    setShowLoading(false);
  }
  const getAllCategories = async () => {
    setShowLoading(true);
    const getAllCategoryRequest = await Axios.post(config.serverApi + 'getalluser',
      {
        params: {
          token: Cookies.get('token')
        }
      })

    if (getAllCategoryRequest.status == "200") {
      getAllCategoryRequest.data.users.map((element) => {
        if (new Date(element.subscriptionEndDate).getTime() < Date.now())
          element.col4 = <div class="col4">Not Found</div>
        else
          element.col4 = <div class="col4">Finish on {String(formatDate(element.subscriptionEndDate))}</div>

      })
    }

    // getAllCategoryRequest.data.data.sort((a, b) => (a.categoryNumber > b.categoryNumber) ? 1 : ((b.categoryNumber > a.categoryNumber) ? -1 : 0));
    setCategories(getAllCategoryRequest.data.users)
    setTimeout(() => setShowLoading(false), 500)
  }

  const categoryHandler = (value) => {
    setValue(value.target.value);
  };


  function formatDate(date) {
    var d = new Date(date),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear();

    if (month.length < 2)
      month = '0' + month;
    if (day.length < 2)
      day = '0' + day;

    return [year, month, day].join('-');
  }



  if (Cookies.get('token'))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="settings" />

        <Container className="wrapper">


          <Row>
            <Col>
              <h1 className="text">Settings</h1>
            </Col>
          </Row>
          <Row>


            <div class="container">
              <div class="row">
                <div class="col-sm">
                  <h3><center>General Color</center></h3>
                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Main Color</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={mainColor} onChange={text => setMainColor(text.target.value)} />
                  </div>

                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Second Color</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={secondColor} onChange={text => setSecondColor(text.target.value)} />
                  </div>

                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Share Button Color</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={shareButton} onChange={text => setShareButton(text.target.value)} />
                  </div>


                </div>
                <div class="col-sm">
                  <h3><center>Quicklinks Color</center></h3>
                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Profile</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={profileColor} onChange={text => setProfileColor(text.target.value)} />
                  </div>

                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Faq</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={faqColor} onChange={text => setFaqColor(text.target.value)} />
                  </div>

                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Saved</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={savedColor} onChange={text => setSavedColor(text.target.value)} />
                  </div>


                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Contact</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={contact} onChange={text => setContact(text.target.value)} />
                  </div>
                </div>
                <div class="col-sm">

                  <h6><center>Other Color</center></h6>
                  <div class="input-group mb-3">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-default">Navigation Color</span>
                    </div>
                    <input type="text" class="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" value={navigationColor} onChange={text => setNavigationColor(text.target.value)} />
                  </div>


                </div>
              </div>
            </div>


            <style jsx>{`
            .wrapper {
              padding: 4rem;
              border: 0;
              width: 80%;
              position: absolute;
              right: 0;
            }
            .text {
              margin-left: 18rem;
              margin-bottom: 4rem;
            }
            .catData {
              transform: translateY(-2.5rem);
            }
            .catButton
            {
              margin-bottom:20px
            }
            table{
              text-align:center;
            }
          `}</style>
          </Row>
          <Row>
            <Col style={{ width: 200 }}>
              <Button variant="primary" type="submit" onClick={() => changeSettings()}>
                Change Settings
          </Button>
            </Col>
          </Row>
        </Container>

      </div >
    );
  else
    window.location.href = '/'


}



