import React, { useEffect, useState } from "react";
import {
  InputGroup,
  Button,
  FormControl,
  Form,
  Row,
  Container,
  Col,
} from "react-bootstrap";
import Table from "./AdminTable";
import Cookies from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import $ from "jquery";
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import Sidebar from './SideNav';
import Loading from './Loading';


export default function Videos(props) {


  const [value, setValue] = useState("");
  const [categories, setCategories] = useState([]);
  const [allCategories, setAllCategories] = useState([]);
  const [showCategoryEditModal, setShowCategoryEditModal] = useState(false);
  const [editCurrentCategory, setEditCurrentCategory] = useState({});
  const [addCategoryObj, setAddCategoryObj] = useState({});
  const [showLoading, setShowLoading] = useState(false);
  const [showAddVideoModal, setShowAddVideoModal] = useState(false);
  const [categoryId, setCategoryId] = useState("");
  useEffect(() => {
    getAllVideos();
    getAllCategories();
    $('#myModal').on('shown.bs.modal', function () {
      $('#myInput').trigger('focus')
    })
    return () => {

    }
  }, [])



  const getAllVideos = async () => {
    setShowLoading(true);
    const getAllCategoryRequest = await Axios.get(config.serverApi + 'getalladmins',
      {
        token: Cookies.get('token'),
      })

    if (getAllCategoryRequest.status == "200") {
      getAllCategoryRequest.data.data.map((element) => {

        element.operations = <div class="col4">   <Button onClick={() => openEditCategoryModal(element)} type="button" class="btn btn-primary">Edit</Button>  <Button type="button" onClick={() => removeCategory(element._id)} className="btn btn-danger">Remove</Button></div>

      })

      // getAllCategoryRequest.data.data.sort((a, b) => (a.categoryNumber > b.categoryNumber) ? 1 : ((b.categoryNumber > a.categoryNumber) ? -1 : 0));
      setCategories(getAllCategoryRequest.data.data)
    }
    setTimeout(() => setShowLoading(false), 500)
  }

  const getAllCategories = async () => {

    const getAllCategoryRequest = await Axios.get(config.serverApi + 'getallcategories',
      {
        token: Cookies.get('token'),
      })
    setAllCategories(getAllCategoryRequest.data.data)

  }






  const addAdmin = async () => {

    const adddCategoryRequest = await Axios.post(config.serverApi + 'addadmin',
      {
        token: Cookies.get('token'),
        email: addCategoryObj.email,
        firstName: addCategoryObj.firstName,
        lastName: addCategoryObj.lastName,
        password: addCategoryObj.password,
      })
    closeAddVideoModal();
    // setCategories((prev) => prev.concat({ categoryNumber: categoryNumber, categoryName: value }));
    getAllVideos();

  };

  const editCategoryModal = () =>

    <Modal show={showCategoryEditModal}>
      <Modal.Header>Edit Video</Modal.Header>
      <Modal.Body>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Email &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Number"
            value={editCurrentCategory.email}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, email: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">First Name &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            value={editCurrentCategory.firstName}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, firstName: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Last Name &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Name"
            value={editCurrentCategory.lastName}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, lastName: element.target.value }))}
          />
        </InputGroup>




      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => saveEditCategory()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeEditCategoryModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const addNewCategoryModal = () =>

    <Modal show={showAddVideoModal}>
      <Modal.Header>Add Admin</Modal.Header>
      <Modal.Body>

        {/* <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Video Number</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Category Number"
            value={editCurrentCategory.videoNumber}
            onChange={(element) => setEditCurrentCategory(prev => ({ ...prev, videoNumber: element.target.value }))}
          />
        </InputGroup> */}

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Email &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Email"
            // value={editCurrentCategory.videoName}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, email: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">First Name &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="First Name"
            // value={editCurrentCategory.videoSource}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, firstName: element.target.value }))}
          />
        </InputGroup>

        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Last Name &nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Last Name"
            // value={editCurrentCategory.videoSource}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, lastName: element.target.value }))}
          />
        </InputGroup>


        <InputGroup className="mb-3">
          <InputGroup.Prepend>
            <InputGroup.Text id="basic-addon1">Password &nbsp;&nbsp;&nbsp;</InputGroup.Text>
          </InputGroup.Prepend>
          <FormControl
            placeholder="Password"
            type="password"
            // value={editCurrentCategory.videoSource}
            onChange={(element) => setAddCategoryObj(prev => ({ ...prev, password: element.target.value }))}
          />
        </InputGroup>






      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => addAdmin()}>Save</Button>
        <Button className="btn btn-danger" onClick={() => closeAddVideoModal()}>Cancel</Button>
      </Modal.Footer>
    </Modal>




  const openEditCategoryModal = (category) => {
    setShowCategoryEditModal(true);
    setEditCurrentCategory(category);
  }

  const closeEditCategoryModal = () => {
    setShowCategoryEditModal(false);
  }
  const closeAddVideoModal = () => {
    setShowAddVideoModal(false);
  }
  const saveEditCategory = async () => {
    console.log({
      adminId: editCurrentCategory._id,
      firstName: editCurrentCategory.firstName,
      lastName: editCurrentCategory.lastName,
      email: editCurrentCategory.email
    })
    const editCategoryRequest = await Axios.post(config.serverApi + 'updateadmin',
      {
        adminId: editCurrentCategory._id,
        firstName: editCurrentCategory.firstName,
        lastName: editCurrentCategory.lastName,
        email: editCurrentCategory.email
      });
    closeEditCategoryModal();
    getAllVideos();
  }

  const removeCategory = async (_id) => {
    const removeCategoryRequest = await Axios.post(config.serverApi + 'deleteadmin',
      {
        adminId: _id
      });
    closeEditCategoryModal();
    getAllVideos();
  }

  if (Cookies.get('token'))
    return (
      <div>
        <Loading show={showLoading} />
        <Sidebar page="admin" />
        {editCategoryModal()}
        {addNewCategoryModal()}
        <Container className="wrapper">

          <Row>
            <Col>
              <h1 className="text">Videos</h1>
            </Col>
          </Row>
          <Row>

            <Col><Button className="btn btn-success" onClick={() => setShowAddVideoModal(true)}>Add Admin</Button><br /><br /></Col>



            <style jsx>{`
            .controlheader
            {
              display:flex
            }
            .wrapper {
              padding: 4rem;
              border: 0;
              width: 80%;
              position: absolute;
              right: 0;
            }
            .text {
              margin-left: 18rem;
              margin-bottom: 4rem;
            }
            .catData {
              transform: translateY(-2.5rem);
            }
            .catButton
            {
              margin-bottom:20px
            }
            table{
              text-align:center;
            }
          `}</style>
          </Row>
          <Row>
            <Col>
              <Table categories={categories} />
            </Col>
          </Row>
        </Container>
      </div>
    );
  else
    window.location.href = '/'




}



