import React, { useState, useEffect } from "react";
import { Form, Button } from "react-bootstrap";
import Cookies, { set } from 'js-cookie';
import Axios from "axios";
import config from '../config.json';
import { useHistory } from 'react-router-dom';

export default function LoginScreen() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showError, setShowError] = useState(false);

  const login = async () => {
    const loginRequest = Axios.post(config.serverApi + 'adminlogin', {
      email: email,
      password: password
    }
    ).then((loginRequest) => {
      if (loginRequest.status == "200") {
        Cookies.set('token', loginRequest.data.token) // save token as a cookie
        window.location.href = 'categories'

      }
      else setShowError(true)
    }).catch(() => {
      setShowError(true)
    })



    // var url = new URL("http://localhost:1337/api/adminlogin"),
    //   params = { email, password }
    // Object.keys(params).forEach(key => url.searchParams.append(key, params[key]))
    // fetch(url).then(e => console.log(e))


    // (async () => {
    //   const rawResponse = await fetch('http://localhost:1337/api/adminlogin', {
    //     method: 'POST',
    //     headers: {
    //       'Accept': 'application/json',
    //       'Content-Type': 'application/json',
    //     },
    //     body: JSON.stringify({ email, password })
    //   });
    //   const result = await rawResponse.json();

    //   document.cookie = `token=${result.token}`
    //   console.log(rawResponse.status);
    // })();


  };
  if (!Cookies.get('token'))
    return (
      <div>

        <div className="formWrapper">
          <Form.Group controlId="formBasicEmail">
            {showError && <div class="alert alert-danger" role="alert">
              The Email or Password wrong !
        </div>}
            <Form.Label>Email address</Form.Label>
            <Form.Control type="email" placeholder="Enter email" onChange={email => setEmail(email.target.value)} />
          </Form.Group>

          <Form.Group controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password" onChange={password => setPassword(password.target.value)} />
          </Form.Group>

          <Button variant="primary" type="submit" onClick={() => login()}>
            Login
          </Button>


        </div>
        <style jsx>{`
        alert-danger{

        }
        .formWrapper {
          margin: 19rem auto;
          padding: 35px 33px;
    background: #ffffffc7;
    border-radius: 10px;
    max-width: 34%;
        }
        body
        {
          background-image: url(https://www.expatica.com/app/uploads/2018/11/find-a-doctor-abroad-1920x1080.jpg) !important;
        }
      `}</style>
      </div>
    );
  else
    window.location.href = 'categories'
}
